<template>
  <div id="ipi-board">
    <div class="left">
      <div class="title" style="text-align:center">
        <h2>页面维护-Q&A</h2>
      </div>
      <div class="left-content">
        <h3>请选择店铺</h3>
        <!-- 菜单 -->
        <el-menu class="el-menu-vertical-demo" unique-opened>
          <el-submenu
            :index="index + ''"
            v-for="(item, index) in menuPlatFormList"
            :key="index"
          >
            <template slot="title">
              <span
                style="display:inline-block;width:100%"
                @click="onceClick(index)"
                >{{ item }}</span
              >
            </template>
            <el-submenu
              :index="index + '-' + index2"
              v-for="(item2, index2) in showCountryList"
              :key="index2"
            >
              <template slot="title">
                <span
                  style="display:inline-block;width:100%"
                  @click="secondClick(index2)"
                  >{{ item2 }}</span
                >
              </template>
              <el-menu-item
                :index="index + '-' + index2 + '-' + index3"
                v-for="(item3, index3) in lastSiteList"
                :key="index3"
                @click="thirdClick(item, item2, item3)"
              >
                <span>{{ item3 }}</span>
              </el-menu-item>
            </el-submenu>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <div class="right">
      <div class="button">
        <el-button type="primary" size="mini" @click="handleAll"
          >全部</el-button
        >
        <el-button type="primary" size="mini" @click="handleBefore"
          >未处理</el-button
        >
        <el-button type="primary" size="mini" @click="handleAfter"
          >已处理</el-button
        >
      </div>
      <div class="table">
        <el-table
          ref="QATable"
          :data="questionList"
          border
          style="width: 100%"
          max-height="750"
        >
          <el-table-column type="index"></el-table-column>
          <el-table-column prop="2" label="站点" width="100"></el-table-column>
          <el-table-column prop="3" label="国家" width="100"></el-table-column>
          <el-table-column prop="4" label="邮件标题"></el-table-column>
          <el-table-column
            prop="5"
            label="发件时间"
            width="250"
          ></el-table-column>
          <el-table-column prop="6" label="回复情况" width="200">
            <template slot-scope="scope">
              <span v-if="scope.row[6] == '已处理'">已回复</span>
              <el-link type="danger" v-else @click="goHandle(scope.row[0])"
                >未处理</el-link
              >
            </template>
          </el-table-column>
          <el-table-column
            prop="7"
            label="回复人"
            width="150"
          ></el-table-column>
          <el-table-column
            prop="8"
            label="回复时间"
            width="200"
          ></el-table-column>
        </el-table>
        <el-pagination
          style="text-align:center;margin-top:5px;"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total,prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      // 左侧导航栏菜单
      menuPlatFormList: [],
      menuCountryList: [],
      menuSiteList: [],
      showCountryList: [],
      showSiteList: [],
      lastSiteList: [],
      // 选中的菜单项
      checkedPlatform: "",
      checkedCountry: "",
      checkedSite: "",
      checkedStatus: "",

      // 问题列表
      questionList: [],
      // 分页相关
      currentPage: 1,
      total: 0,
      pageSize: 50
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取菜单栏数据
    getMenuList() {
      this.$api.getMenuList().then(res => {
        // console.log(res);
        this.menuPlatFormList = res.data.data1;
        this.menuCountryList = res.data.data2;
        this.menuSiteList = res.data.data3;
      });
    },
    onceClick(index) {
      this.showCountryList = [];
      this.showSiteList = [];
      this.showCountryList = this.menuCountryList[index];
      this.showSiteList = this.menuSiteList[index];
    },
    secondClick(index) {
      // console.log(index);
      this.lastSiteList = [];
      this.lastSiteList = this.showSiteList[index];
    },
    thirdClick(platform, country, site) {
      this.checkedPlatform = platform;
      this.checkedCountry = country;
      this.checkedSite = site;
      this.checkedStatus = "";
      let page = 1;
      this.$api.queryQandA(platform, country, site, page).then(res => {
        console.log(res);
        this.questionList = res.data.data;
        this.questionList = this.questionListSort(this.questionList);
        this.total = res.data.all_number;
        this.$nextTick(() => {
          this.$refs["QATable"].bodyWrapper.scrollTop = 0;
        });
        this.currentPage = 1;
      });
    },
    // 封装方法，将questionList未处理放前面
    questionListSort(arr) {
      let newArr = [];
      // 将未处理的先push进新的数组，同时，旧的数组要删除未处理的一项，最后旧的数组就只剩已处理的
      for (let i = 0; i < arr.length; i++) {
        if (arr[i][10] == "未处理") {
          newArr.push(arr[i]);
          arr.splice(i, 1);
          i--;
        }
      }
      // 将只剩已处理的旧数组push进新数组
      newArr.push(...arr);
      return newArr;
    },
    // 下拉框联合
    selectChange(val) {
      // console.log(val);
    },
    // 未处理
    handleBefore() {
      this.checkedStatus = "未处理";
      let page = 1;
      this.$api
        .queryQandA(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          page,
          this.checkedStatus
        )
        .then(res => {
          // console.log(res);
          this.questionList = res.data.data;
          this.$nextTick(() => {
            this.$refs["QATable"].bodyWrapper.scrollTop = 0;
          });
          this.currentPage = 1;
          this.total = res.data.all_number;
        });
      // if (
      //   this.checkedPlatform.length > 0 &&
      //   this.checkedCountry.length > 0 &&
      //   this.checkedSite.length > 0
      // ) {

      // } else {
      //   return;
      // }
    },
    // 已处理
    handleAfter() {
      this.checkedStatus = "已处理";
      let page = 1;
      this.$api
        .queryQandA(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          page,
          this.checkedStatus
        )
        .then(res => {
          // console.log(res);
          this.questionList = res.data.data;
          this.$nextTick(() => {
            this.$refs["QATable"].bodyWrapper.scrollTop = 0;
          });
          this.currentPage = 1;
          this.total = res.data.all_number;
        });
      // if (
      //   this.checkedPlatform.length > 0 &&
      //   this.checkedCountry.length > 0 &&
      //   this.checkedSite.length > 0
      // ) {

      // } else {
      //   return;
      // }
    },
    handleAll() {
      let page = 1;
      this.checkedStatus = "";
      this.$api
        .queryQandA(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          page
        )
        .then(res => {
          // console.log(res);
          this.questionList = res.data.data;
          this.questionList = this.questionListSort(this.questionList);
          this.$nextTick(() => {
            this.$refs["QATable"].bodyWrapper.scrollTop = 0;
          });
          this.currentPage = 1;
          this.total = res.data.all_number;
        });
      // if (
      //   this.checkedPlatform.length > 0 &&
      //   this.checkedCountry.length > 0 &&
      //   this.checkedSite.length > 0
      // ) {

      // } else {
      //   return;
      // }
    },
    // 去回复
    goHandle(id) {
      console.log(id);

      this.$confirm("确定回复吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$api.updateSatus(id).then(res => {
            console.log(res);
            this.thirdClick(
              this.checkedPlatform,
              this.checkedCountry,
              this.checkedSite
            );
            this.$message.success("回复成功");
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消回复"
          });
        });
    },
    // 分页
    handleCurrentChange(val) {
      console.log(val);
      this.$api
        .queryQandA(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          val,
          this.checkedStatus
        )
        .then(res => {
          this.questionList = res.data.data;
          this.total = res.data.all_number;
          this.$nextTick(() => {
            this.$refs["QATable"].bodyWrapper.scrollTop = 0;
          });
        });
    }
  },
  created() {
    this.getMenuList();
    this.thirdClick("", "", "");
  },
  mounted() {}
};
</script>
<style scoped>
#ipi-board {
  width: 100vw;
  height: 92vh;
  position: relative;
  /* margin-top: 2vh; */
}
.left {
  position: absolute;
  width: 16vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  border-right: 1px solid black;
  top: 0;
  left: 0;
  overflow: auto;
}
.right {
  position: absolute;
  top: 0;
  width: 80vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  left: 18vw;
  padding: 24px;
  /* overflow: auto; */
}
.left-content h3 {
  margin-top: 2vh;
  text-align: center;
}
.button {
  margin-bottom: 2vh;
}
</style>



